.header {
  width: 100%;
  background-color: rgb(250, 250, 250);
  height: 70px;
  display: block;
}

.inner_header {
  width: 800px;
  background-color: rgb(250, 250, 250);
  display: block;
  margin: 0 auto;
  height: 100%;
}

.name {
  height: 100%;
  display: table;
  float: left;
}

.name h1 {
  color: black;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
}

.nav {
  float: right;
  height: 100%;
}

.div_ref {
  height: 100%;
  display: table;
  padding: 0px 20px;  
}

.div_ref li {
  display: table-cell;
  vertical-align: middle;
  padding: 8px;
}


/* body {
  background-color: rgb(255, 255, 255);
} */


a:link, a:visited {
  color: black;
}


a:hover {
  color:darkblue;
}

*{
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}


.content {
    width: 800px;
    background-color: rgb(255, 255, 255);
    display: block;
    margin: 0 auto;
    height: 100%;
}

.gap {
  padding: 0;
}